 .root {
   display: flex;
 }

 .navBar {
   flex: 0 0 10%;
   min-height: 100vh;
   background-color: #1A1053;
   color: rgba(255, 255, 255, .9);
   /* color: (props)=> props.isDarkMode ? "rgba(255,255,255,.87)": "rgba(255,255,255,.9)", */
   text-transform: capitalize;
   display: flex;
   justify-content: center;
 }

 .mainContent {
   flex: 1;
   padding: 4rem;
   position: relative;
   overflow: hidden;
 }